.info-list{
    display: flex;
    flex-direction: column;
    grid-gap: clamp(25px, 4.386vw, 75px);

    &__text{
        color: rgba(var(--cl-a),1.0);
        font-family: var(--font-family);
        font-size: 23px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 180%;
        text-align: left;  
        letter-spacing: -2px;

        b{
            font-weight: 500;
            font-style: italic;
            font-family: var(--font-family2);
        }

        @media (max-width: (1550px)) {
            font-size: 20px;
          }
          
        @include small-tablet{
            font-size: 15px;
            grid-gap: clamp(20px, 2.632vw, 45px);
            letter-spacing: normal;
            line-height: normal;
        }
    }
}